
@charset "UTF-8";
/* =====================

グローバルのスタイル

===================== */
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  width: 80%;
  margin: auto;
}
.global-nav {
  position: fixed;
  top: 0;
  right: 0;
  margin: 20px 40px;
}
.global-nav ul {
    padding: 0;
    text-align: right;
    list-style: none;
}
.global-nav ul a {
      text-decoration: none;
      color: #35495e;
      font-weight: bold;
      -webkit-transition: 0.3s;
      transition: 0.3s;
      position: relative;
}
.global-nav ul a:before, .global-nav ul a:after {
        content: "";
        width: 0px;
        height: 2px;
        background: #41b882;
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        -webkit-transition: 0.2s;
        transition: 0.2s;
}
.global-nav ul a:before {
        left: calc(100% + 5px);
}
.global-nav ul a:after {
        right: calc(100% + 5px);
}
.global-nav ul a:hover {
        opacity: 0.8;
}
.global-nav ul a:hover:before, .global-nav ul a:hover:after {
          width: 10px;
}
.fade-enter-active {
  -webkit-transition: opacity 2s;
  transition: opacity 2s;
}
.fade-enter,
.fade-leave {
  opacity: 0;
}
.main-content {
  margin: 40px 0;
  padding: 20px;
  border: 1px solid #aaa;
}

p[data-v-bc3f03c6] {
  width: 80%;
  margin: auto;
  margin-bottom: 20px;
}

.sub-nav {
  margin: auto;
}
.sub-nav:after {
    content: "";
    display: table;
    clear: both;
}
.sub-nav ul {
    padding: 0;
    list-style: none;
}
.sub-nav ul li {
      width: 33.3%;
      float: left;
}
.sub-nav ul li a {
        display: block;
        padding: 10px 0;
        text-decoration: none;
        color: #35495e;
        font-weight: bold;
        -webkit-transition: 0.3s;
        transition: 0.3s;
        position: relative;
}
.sub-nav ul li a:before {
          content: "";
          width: 0px;
          height: 2px;
          background: #41b882;
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
          -webkit-transition: 0.5s;
          transition: 0.5s;
}
.sub-nav ul li a:hover {
          opacity: 0.6;
}
.sub-nav ul li a:hover:before {
            width: 50%;
}
.sub-content {
  border: 1px solid #41b882;
  background-color: #41b882;
  margin: 20px;
}
.sub-content .sub-title {
    color: white;
}
.sub-content p {
    width: 80%;
    margin: auto;
    margin-bottom: 20px;
    color: white;
}
